* {
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

li {
  list-style-type: none;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 100%;
  background-color: white;
  z-index: 2;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 98%;
}

.nav-bar button {
  cursor: pointer;
}

.logo {
  width: 48px;
  height: 48px;
  background-color: transparent;
  border: none;
  margin-right: 1px;
}

.logo:hover {
  background-color: #e9e9e9;
  border-radius: calc(48px / 2);
}

.home-button,
.create-button {
  font-size: 1rem;
  font-weight: 500;
  width: 80px;
  height: 45px;
  border: transparent;
  background-color: transparent;
}

.create-button {
  margin-right: 6px;
}

.active-button {
  color: white;
  background-color: black;
  border-radius: 25px;
}

.search-bar {
  display: flex;
  justify-content: space-evenly;
  width: 70%;
  height: 45px;
  border-radius: 25px;
  background-color: rgb(241, 241, 241);
}

.search-bar:hover {
  background-color: rgb(225, 225, 225);
}

.search-icon {
  width: 5%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.search-input {
  width: 95%;
  border: none;
  background-color: transparent;
  font-size: 1rem;
}

.search-input:focus {
  outline: none;
}

.nav-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15%;
  height: 100%;
  margin-left: 10px;
}

.nav-buttons button {
  margin-right: 1px;
}

.nav-buttons button:not(:last-child) {
  border: none;
  background-color: transparent;
  color: #767676;
  width: 50px;
  height: 50px;
  border-radius: 25px;
}

.nav-buttons button:last-child {
  border: none;
  background-color: transparent;
  color: #767676;
  width: 25px;
  height: 25px;
  border-radius: calc(25px / 2);
}

.nav-buttons button:hover {
  background-color: #e9e9e9;
}

.content {
  width: 100%;
  height: fit-content;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.posts {
  width: 93%;
  columns: 5 200px;
  column-gap: 20px;
}

.post {
  margin-bottom: 20px;
  position: relative;
}
.post:hover {
  cursor: pointer;
}

.post img {
  border-radius: 15px;
  width: 100%;
  height: 100%;
}

.hovered-post {
  display: none;
}

.black-fade {
  background-color: rgb(0, 0, 0, 0.3);
  width: 100%;
  height: 99%;
  top: 0;
  position: absolute;
  border-radius: 15px;
}

.hovered-post button {
  cursor: pointer;
}

.save-post-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 70px;
  height: 45px;
  border: transparent;
  border-radius: 30px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1rem;
  font-weight: 600;
  background-color: #e60023;
  color: white;
}

.save-post-btn:hover {
  background-color: #cc0000;
}

.post-buttons {
  display: flex;
  justify-content: space-evenly;
  position: absolute;
  bottom: 15px;
  right: 0px;
  width: 90px;
}

.post-buttons button {
  border: transparent;
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

/* CREATE */

.create {
  display: flex;
  /* justify-content: center; */
  width: 100%;
  height: fit-content;
  margin-top: 80px;
}

.boards {
  /* display: none; */
  border-top: #cdcdcd 1px solid;
  position: fixed;
  width: 80px;
  /* width: 350px; */
  height: calc(100vh - 81px);
  border-right: #cdcdcd 1px solid;
  background-color: white;
}

.boards-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 135px;
  border-bottom: #cdcdcdcd 1px solid;
}

.boards-menu button:not(.create-new-button-text) {
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
}

.boards-menu button:not(.create-new-button-text):hover {
  background-color: #e9e9e9;
}

.create-new-button-text {
  width: 100%;
  height: 60%;
  border-radius: 20px;
  background-color: #e9e9e9;
  font-size: 1rem;
  font-weight: 500;
  border: transparent;
  cursor: pointer;
}

.create-new-button-text:hover {
  background-color: #e2e2e2;
}

.drafts-container {
  display: flex;
  flex-direction: column-reverse;
  justify-content: baseline;
  align-items: center;
  margin-top: 20px;
}

.draft {
  display: flex;
  justify-content: baseline;
  align-items: center;
  height: 90px;
  width: 95%;
  border: transparent 1px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.draft div:first-child {
  width: 70px;
  height: 70px;
  border: transparent;
  overflow: hidden;
  position: relative;
  border-radius: 10px;
  margin-left: 10px;
  margin-right: 10px;
}

.draft img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block; /* Remove any inline spacing */
  position: absolute;
}

.active-draft {
  background-color: #e2e2e2;
  border: 1px solid black;
}

/* Pin Composer */
.pin-composer {
  margin-left: 81px;
  /* margin-left: 351px; */
  width: calc(100% - 80px);
  height: 100%;
}

.pin-composer-header {
  position: fixed;
  width: 100%;
  height: 80px;
  border-top: #cdcdcd 1px solid;
  border-bottom: #cdcdcd 1px solid;
  background-color: white;
  display: flex;
  align-items: center;
  z-index: 2;
}

.pin-composer-header p {
  font-size: 1.25rem;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
  padding: 0 30px;
}

.pin-creation-area {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: calc(100% - 80px);
  padding: 20px 0;
  margin-top: 82px;
}

.pin-creation-container {
  display: flex;
  width: 83%;
  height: 100%;
  z-index: 1;
}

.choose-drag-file {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: baseline;
  width: 430px;
  height: 100%;
}

.box-placeholder {
  position: relative;
  width: 350px;
  height: 500px;
  border: 2px solid #cdcdcd;
  border-radius: 30px;
  border-style: dashed;
  background-color: #e9e9e9;
  margin-bottom: 30px;
}

.box-placeholder img {
  /* position: absolute;
  top: 0;
  left: 0; */
  width: 100%;
  height: auto;
  border-radius: 30px;
}

.box-placeholder input {
  width: 100%;
  height: 100%;
  /* background-color: yellow; */
}

.box-placeholder button {
  border: none;
  background-color: transparent;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.instruction-1 {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1rem;
}

.instruction-2 {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px;
}

.choose-drag-file > button {
  width: 370px;
  height: 50px;
  background-color: #e9e9e9;
  border: transparent;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: 500;
}

.posting-form {
  width: calc(100% - 500px);
  /* width: 60%; */
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: baseline;
}

.posting-form p {
  font-size: 0.8rem;
  margin-bottom: 10px;
  color: #000000;
}

.posting-form input {
  font-size: 1rem;
  width: calc(100% - 20px);
  height: 40px;
  border: 2px solid #cdcdcd;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 0 10px;
}

.posting-form input::placeholder {
  color: #767676;
  font-size: 1rem;
}

.posting-form input:disabled::placeholder {
  color: #cdcdcd;
}

.posting-form .input-description {
  height: 70px;
  margin-bottom: 40px;
}
